	import {
			mapState,
			mapMutations
		} from 'vuex';
	import apiName from '../../static/commonjs/request/apiName.js'
	export default {
		data() {
			return {
				pageNum:1,
				jobList:[
// 					{
// 						invite_id:'1',
// 						company_name:'信驰科技',
// 						station:'黄泥磅',
// 						auditiontime:'2020-01-10',
// 						
