// 					{
// 						invite_id:'1',
// 						company_name:'伟鑫科技',
// 						station:'大坪',
// 						auditiontime:'2020-01-12',
// 						
// 					}
				]
			}
		},
		// created() {
		// 	this.getInviteList()
		// },
		onShow() {
			this.getInviteList()
		},
		computed: {
			...mapState(['token','presentAddress','jobCategory','indexSearchJob'])
		},
		methods: {
			
			// onReachBottom(){
			// 	this.getInviteList()
			// },
			toDetail(id){
				uni.navigateTo({
				    url: `/pages/inviteDetail/inviteDetail?id=${id}`
				})
			},
			goBack(){
				uni.navigateBack()
			},
			getInviteList(){
				this.$http.post(apiName.INVITE_LIST,{
					params:{
						// ver:'v1.0',
						// apiname:apiName.INVITE_LIST,
						token:this.token,
						page_pageno:this.pageNum,
						page_pagesize:10
					}
				}).then(res => {
					if(res.data.data){
						let data = res.data.data
						let list = this.jobList
						this.pageNum = this.pageNum + 1
						this.jobList = list.concat(data)
					}
				}).catch(err => {
					console.log(err);
				})
			}
		}
	}
